import React, { Component } from 'react'
import Item from './Item'


export class Helper extends Component {
    state = {
        items : [
            {id : 0 , title : "sssssss", },
            {id : 1 , title : "sssssss", },
            {id : 2 , title : "sssssss", },
            {id : 3 , title : "sssssss", },
            {id : 4 , title : "sssssss", }
        ]
    }
    render() {
        return (
            <div>
                {this.state.items.map(item => <Item key={item.id} id={item.id} title={item.title} />)}
            </div>
        )
    }
}

export default Helper
